.library{
    position: fixed;
    background: #fff;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    overflow: auto;
    box-shadow: 2px 2px 50px rgb(211, 205, 205);
    h2{
        padding: 2rem;
    }
}

.library-song{
    display: flex;
    padding: 1rem 2rem 1rem 2rem;
    align-items: center;
    cursor: pointer;
    transition: background-color 0.5s ease;
    img{
        width: 30%;
    }
    &:hover{
        background-color: rgb(222, 222, 255);
    }
}
.library-song{
    padding-left: 1rem;
}

.song-description{
    padding-left: 1rem;
    h3{
        font-size: 1rem;
    }
    h4{
        font-size: 0.7rem;
    }
}

*{
    scrollbar-width: thin;
    scrollbar-color: rgba(155,155,155, 0.5) transparent;
}

*::-webkit-scrollbar{
    width: 5px;
}
*::-webkit-scrollbar-track{
    background: transparent;
}

*::-webkit-scrollbar-thumb{
    background-color:  rgba(155,155,155, 0.5);
    border-radius: 20px;
    border:transparent;
}

.selected{
    background-color: rgb(165, 181, 228);
}

.active-library{
    transform: translateX(0);
    opacity: 1;
}

@media screen and (max-width:768px){
    .library{
        width: 100%;
    }
}